#banner_product{
    position: absolute;
    bottom: 0%;
}

.GVKS_Contents_box_title{
    margin-left: 5%;
    margin-top: 60px;
}

.GVKS_Contents_box_title > h1{
    margin: 0px;
    font-size: 40px;
}

.GVKS_Contents_box_info{
    display: flex;
}

.GVKS_Contents_box_title > span{
    font-size: 20px;
    font-weight: bold;
}

.GVKS_Contents_box_info{
    margin-top: 20px;
}

.GVKS_Contents_box_info_img{
    margin-left: 5%;
    display: flex;
    justify-content: space-between;
}

.GVKS_Contents_box_info_img_img{
    height: 540px;
    width: auto;
}

.GVKS_Contents_box_img{
    display: flex;
    justify-content: space-between;
    margin-top: 120px;
}

.GVKS_Contents_box_img_img_img{
    height: 480px;
    width: auto;
}

.GVKS_Contents_box_img{
    margin-left: 5%;
    margin-right: 5%;
}

.Brochure{
    margin-top: 120px;
    text-align: center;
}

.Brochure > h1{
    font-size: 40px;
}

.Bro_Img{
    margin-top: 20px;
    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-around;
}


.Bro_Down{
    text-decoration: none; 
    color: white;
    background-color: rgb(76, 215, 150); 
    line-height: 65px; 
    font-size: 25px;
    width: 80px;
    height: 40px;
}

.Bro_Img_img{
    height: 650px;
    width: auto;
}

